<template>
  <v-app-bar app>
    <v-btn fab small elevation="1" class="mr-2" @click="setDrawer(!drawer)">
      <v-icon v-if="drawer">mdi-dots-vertical</v-icon>
      <v-icon v-else>mdi-view-dashboard</v-icon>
    </v-btn>
    <v-toolbar-title>
      {{$route.name}}
    </v-toolbar-title>
    <v-spacer></v-spacer>
    <v-btn icon>
      <v-icon>mdi-heart</v-icon>
    </v-btn>
    <v-btn icon>
      <v-icon>mdi-magnify</v-icon>
    </v-btn>
    
    <v-menu 
      offset-y
      left 
      bottom
      origin="right top"
      transition="scale-transition"
      nudge-bottom="8"
      nudge-left="10"
      >
      <template v-slot:activator="{ on, attrs }">
        <v-btn icon v-bind="attrs" v-on="on">
          <v-icon>mdi-dots-vertical</v-icon>
        </v-btn>
      </template>

      <v-list>

        <v-list-item  @click="() => {}">
          <v-list-item-action>
            <v-icon>
              mdi-account-settings
            </v-icon>
          </v-list-item-action>
          <v-list-item-title>
            个人信息
          </v-list-item-title>
        </v-list-item>


         <v-list-item  @click="() => {}">
          <v-list-item-action>
            <v-icon>
              mdi-lock-check
            </v-icon>
          </v-list-item-action>
          <v-list-item-title>
            修改密码
          </v-list-item-title>
        </v-list-item>


         <v-list-item  @click="logout">
          <v-list-item-action>
            <v-icon>
              mdi-exit-to-app
            </v-icon>
          </v-list-item-action>
          <v-list-item-title>
            退出
          </v-list-item-title>
        </v-list-item>

      </v-list>
    </v-menu>
    
  </v-app-bar>
</template>

<script>
import { mapState, mapMutations } from "vuex";
import account from "@/api/service/account.js";

export default {
  name: "DashboardAppBar",
  computed: {
    ...mapState(["drawer"]), // 将 定义的状态映射为同名的计算属性
  },
  methods: {
    ...mapMutations({
      setDrawer: "SET_DRAWER", // 将定义的状态方法 'SET_DRAWER' 映射为 setDrawer
    }),

  logout(){
    account.logout();    
    this.$router.push('/account');
  },


  },
};
</script>

